/* /css/background.css */

#backgrouund {
  position: fixed;
  left: 0;
  top:0;
  height: 100vh;
  width:100%;

  background-attachment: fixed;
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;

  opacity:0;
  transition: opacity 0.75s linear;

  z-index: -1;
}

#background > img {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
}